<template>
	<div class="chooser-component">
		<ul class="chooser-list">
			<li v-for="(item, index) in versionList" @click="toggleCheck(index)" :class="{active:checkActive(index)}" :title="item.label">{{ item.label }}</li>
		</ul>
	</div>
</template>

<script>
	import _ from 'lodash'

	export default {
		props:{
			versionList:{
				type:Array,	
				default:[
					{
						label: '客户版',
			          	value: 0
					}
				]
			}
		}, 
		data (){
			return {
				// checkActive: true,
				nowIndexes: [0]
			}
		},
		methods:{
			toggleCheck (index){
				
				if(this.nowIndexes.indexOf(index) === -1){
					this.nowIndexes.push(index)
				}else{
					this.nowIndexes = _.remove(this.nowIndexes, (idx) => {
						return idx !== index
					})
				} 

				let newObjArray = _.map(this.nowIndexes, (idx) => {
					return this.versionList[idx]
				})
				
				this.$emit('on-change', newObjArray)
			},
			checkActive (index) {
		        return this.nowIndexes.indexOf(index) !== -1
		    }
		}
	}
</script>

<style scoped>
.chooser-component {
  position: relative;
  display: inline-block;
}
.chooser-list li{
  display: inline-block;
  border: 1px solid #e3e3e3;
  height: 25px;
  line-height: 25px;
  padding: 0 8px;
  margin-right: 5px;
  border-radius: 3px;
  text-align: center;
  cursor: pointer;
}
.chooser-list li.active {
  border-color: #4fc08d;
  background: #4fc08d;
  color: #fff;
}
</style>


