<template>
	<div class="login-form">
		<div class="g-form">
			<div class="g-form-line">
				<span class="g-form-label">用户名：</span>
				<div class="g-form-input">
					<input type="text" v-model="userName" placeholder="请输入用户名">
					
				</div>
				<span class="g-form-error">{{ userError.errorMsg }}</span>
			</div>
			<div class="g-form-line">
				<span class="g-form-label">密 码：</span>
				<div class="g-form-input">
					<input type="password" v-model="userPassword" placeholder="请输入密码">
				</div>
				<span class="g-form-error">{{ passwordError.errorMsg }}</span>
			</div>
			<div class="g-form-line">
				<span class="g-form-label"></span>
				<div class="g-form-input">
					<input type="button" class="g-form-btn" @click="onLogin" value="登 录">
				</div>
			</div>
			<p>{{ errorMsg }}</p>
		</div>
	</div>
</template>

<script>
	export default {
		data (){
			return {
				userName:'',
				userPassword:'',
				errorMsg:''
			}
		},
		computed:{
			userError (){
				let errorMsg, status;
				if(!/@/g.test(this.userName)){
					status = false;
					errorMsg = '用户名需包含@特殊字符';
				}else{
					status = true;
					errorMsg = '';
				}
				if(!this.userflag){
					errorMsg = '';
					this.userflag = true;
				}
				return {
					errorMsg,
					status
				}
			},
			passwordError (){
				let errorMsg, status;
				if(!/^\w{1,6}$/g.test(this.userPassword)){
					status = false;
					errorMsg = '密码为1-6位的字符';
				}else{
					status = true;
					errorMsg = '';
				}
				if(!this.passwordflag){
					errorMsg = '';
					this.passwordflag = true;
				}
				return {
					errorMsg,
					status
				}
			}
		},
		methods:{
			onLogin (){
				if(!this.userError.status || !this.passwordError.status){
					this.errorMsg = '部分选项验证未通过'
				}else{
					this.errorMsg = '';
					this.$http.get('/api/login').then(function(res){
						console.log(res.data);
						this.$emit('has-log', res.data)
					}, (err) => {
						console.log(err)
					})
				}
			}
		}
	}
</script>


