<template>
	<div>
		<thisDialog v-show="isShowCheckDialog" @on-close="checkStatus">
			请检查你的支付状态！
			<div class="button" @click="checkStatus">
				支付成功
			</div>
			<div class="button">
				支付失败
			</div>
		</thisDialog>

		<thisDialog v-show="isShowSuccessDialog" @on-close="toOrderList">
			购买成功
		</thisDialog>

		<thisDialog v-show="isShowFailDialog">
			购买失败
		</thisDialog>
	</div>
</template>

<script>
	import thisDialog from '../components/dialog' 
	
	export default {
		components:{
			thisDialog
		},
		props:{
			isShowCheckDialog:{
				type:Boolean,
				default:false
			},
			orderId:{
				type:[Number, String]
			}
		},
		data(){
			return {
				isShowSuccessDialog:false,
				isShowFailDialog:false
			}
		},
		methods:{
			checkStatus (){
				this.$http.post('/api/checkOrder', {orderId: this.orderId}).then((data) => {
					// this.isShowCheckDialog = false;
					this.isShowSuccessDialog = true;
					this.$emit('on-close-check-dialog');
				}, (err) => {
					this.isShowFailDialog = true;
					this.$emit('on-close-check-dialog');
				})
			},
			toOrderList (){
				this.$router.push({path: '/orderList'})
			}
		}
	}
</script>


