<template>
	<div class="index-wrap"> 
		<div class="index-left">
			<div class="index-left-block">
				<h2>全部产品</h2>
				<template v-for="production in productionList"> 
					<h3>{{ production.title }}</h3>
					<ul>
						<li v-for="item in production.list">
							<a :href="item.url">{{ item.name }} <span v-if="item.hot" class="hot-tag">Hot</span></a>
						</li>
					</ul>
					<div v-if="!production.last" class="hr"></div>
					
				</template>
			</div>

			<div class="index-left-block lasttest-news">
				<h2>最新消息</h2>
				<ul>
					<li v-for="item in newsList">
						<a :href="item.url" class="new-item">{{ item.title }}</a>
					</li>
				</ul>
			</div>
		</div>

		<div class="index-right">
			<slideShow :slides="slides" :runTime="runTime"></slideShow>
			<div class="index-board-list">
				<div class="index-board-item" v-for="(item, index) in boardList" :class="[{ 'line-last' : index % 2 !== 0 }, 'index-board-' + item.id]">
					<div class="index-board-item-inner">
						<h2>{{ item.title }}</h2>
						<p>{{ item.description }}</p>
						<div class="index-board-button">
							<router-link class="button" :to="item.url">立即购买</router-link>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script> 
	import slideShow from '../components/slideShow'

	export default {
		components:{
			slideShow
		},
		created (){
			this.$http.get('/api/getNewsList').then((res) => {
				// console.log('请求json-server的RETSFUL接口' + JSON.stringify(res.body))
				this.newsList = res.data
			}, (err) => {
				console.log(err)
			})
		},
		data (){
			return {
				slides:[
					{
						src: require('../assets/slideShow/pic1.jpg'),
						title: '图片1标题说明',
						href: '/detailpage/analysis'
					},
					{
						src: require('../assets/slideShow/pic2.jpg'),
						title: '图片2标题说明',
						href: '/detailpage/analysis'
					},
					{
						src: require('../assets/slideShow/pic3.jpg'),
						title: '图片3标题说明',
						href: '/detailpage/analysis'
					},
					{
						src: require('../assets/slideShow/pic4.jpg'),
						title: '图片4标题说明',
						href: '/detailpage/analysis'
					}
					
				],
				runTime:2000,
				boardList:[
					{
						'title':'开放产品',
						'description':'开放产品是一款开放产品',
						'id':'car',
						'url':'/detailpage/analysis',
						'saleout':false
					},
					{
						'title':'品牌营销',
						'description':'开放产品是一款开放产品',
						'id':'loud',
						'url':'/detailpage/analysis',
						'saleout':false
					},
					{
						'title':'使命必达',
						'description':'开放产品是一款开放产品',
						'id':'earth',
						'url':'/detailpage/analysis',
						'saleout':false
					},
					{
						'title':'地球最强',
						'description':'开放产品是一款开放产品',
						'id':'hill',
						'url':'/detailpage/analysis',
						'saleout':false
					}
				],
				newsList:[
					{
						'title':'统计数据',
						'url':'http://starcraft.com'
					},
					{
						'title':'数据预测',
						'hot':true,
						'url':'http://starcraft.com'
					},
					{
						'title':'流量分析',
						'url':'http://starcraft.com'
					},
					{
						'title':'广告预测',
						'url':'http://starcraft.com'
					}
				],
				productionList:{
					pc:{
						title:'PC产品',
						list:[
							{
								'name':'统计数据',
								'url':'http://starcraft.com'
							},
							{
								'name':'数据预测',
								'hot':true,
								'url':'http://starcraft.com'
							},
							{
								'name':'流量分析',
								'url':'http://starcraft.com'
							}
						]
					},
					app:{
						title:'手机应用类',
						last:true,
						list:[
							{
								'name':'91助手',
								'url':'http://starcraft.com'
							},
							{
								'name':'产品助手',
								'url':'http://starcraft.com'
							},
							{
								'name':'智能地图',
								'url':'http://starcraft.com'
							},
							{
								'name':'团队语音',
								'url':'http://phone.com'
							}
						]
					}
				},
				methods:{
					// dosomethingSlideChange (){
					// 	console.log('dosomethingSlideChange run!')
					// }
				}
			}
		}
	}
</script>

<style scoped>
.index-wrap {
  width: 1200px;
  margin: 0 auto;
  overflow: hidden;
}
.index-left {
  float: left;
  width: 300px;
  text-align: left;
}
.index-right {
  float: left;
  width: 900px;
}
.index-left-block {
  margin: 15px;
  background: #fff;
  box-shadow: 0 0 1px #ddd;
}
.index-left-block .hr {
  margin-bottom: 20px;
}
.index-left-block h2 {
  background: #4fc08d;
  color: #fff;
  padding: 10px 15px;
  margin-bottom: 20px;
}
.index-left-block h3 {
  padding: 0 15px 5px 15px;
  font-weight: bold;
  color: #222;
}
.index-left-block ul {
  padding: 10px 15px;
}
.index-left-block li {
  padding: 5px;
}
.index-board-list {
  overflow: hidden;
}
.index-board-item {
  float: left;
  width: 400px;
  background: #fff;
  box-shadow: 0 0 1px #ddd;
  padding: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
}
.index-board-item-inner {
  min-height: 125px;
  padding-left: 120px;
}
.index-board-car .index-board-item-inner{
  background: url(../assets/images/1.png) no-repeat;
}
.index-board-loud .index-board-item-inner{
  background: url(../assets/images/2.png) no-repeat;
}
.index-board-earth .index-board-item-inner{
  background: url(../assets/images/3.png) no-repeat;
}
.index-board-hill .index-board-item-inner{
  background: url(../assets/images/4.png) no-repeat;
}
.index-board-item h2 {
  font-size: 18px;
  font-weight: bold;
  color: #000;
  margin-bottom: 15px;
}
.line-last {
  margin-right: 0;
}
.index-board-button {
  margin-top: 20px;
}
.lastest-news {
  min-height: 512px;
}
.hot-tag {
  background: red;
  color: #fff;
}
.new-item {
  display: inline-block;
  width: 230px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>


