import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import IndexPage from '../pages/index'
import DetailPage from '../pages/detailPage'
import VueResour from 'vue-resource'
import Count from '../components/detail/count'
import OrderList from '../pages/orderList'
import Forecast from '../components/detail/forecast'
import Analysis from '../components/detail/analysis'
import Publish from '../components/detail/publish'
import Testb from '../components/testb'
    
Vue.use(Router)
Vue.use(VueResour)

const router = new Router({
	mode:'history',
  	routes: [
    	{
    		path:'/',
    		component:IndexPage
    	},
        {
            path:'/testb',
            component:Testb
        },
        {
            path:'/detailpage',
            component:DetailPage,
            // redirect:'/detailpage/count',
            children:[
                {
                    path:'count',
                    component:Count,
                    name:'count'
                },
                {
                    path:'forecast',
                    component:Forecast,
                    name:'forecast'
                },
                {
                    path:'analysis',
                    component:Analysis,
                    name:'analysis'
                },
                {
                    path:'publish',
                    component:Publish,
                    name:'publish'
                }
            ]
        },
        // {
        //     path:'/orderlist',
        //     componet:OrderList
        // }
  	]
})

export default router;



